.ant-table-small {
  border: 0 !important;
}

.ant-table-column-title {
  font-family: 'NotoSansKR-Bold';
  color: '#00000055';
  font-weight: 300;
  font-size: 1.05em;
}

.ant-table.ant-table-small thead > tr > th {
  background-color: #66aad7 !important;
  color: #fafafa;
}
th.ant-table-cell {
  font-size: 1.1em;
  font-weight: bold;
  color: #fafafa;
  text-align: center !important;
}
